.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #1E1E25 ;
    height: auto;
    padding-top: 10vh;
    /* width: 100vw; */
}

.heading{
    color: #fff;
    font-family:'Magilio';
    font-size: 4.5rem;
    margin-top:5rem;
}

.content {
    color: #fff;
    text-align: center;
    width: 85vw;
    font-family: 'Montserrat',sans-serif;
    font-size: 1.5rem;
    padding: 1.5rem 0;
}

.Text {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 3rem 0;
}
.numText {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.num{
    font-family: 'Magilio';
    font-size: 4.5rem;
    background: linear-gradient(98.61deg, #0500FF 0%, #F537F9 53.08%, #F7BE2B 96.51%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.text{
    color: #fff;
    font-family: 'Montserrat',sans-serif;
    font-size: 1.5rem;
    text-align: center;
}


@media only screen and (min-width:770px) and (max-width:1280px) {
    img{
        scale: 0.7;
    }
}


@media (min-width:320px) and (max-width: 760px)   {
    /* smartphones, iPhone, portrait 480x320 phones */
     .heading{
        font-size: 2.5rem;
        width: 69vw;
        text-align: center;
     }
     .content{
        font-size: 0.8rem;
     }
     .Text{
        width: 85vw;
        justify-content: space-between;
        margin-top: 0;
     }
     .num{
        font-size: 2rem;
     }
     .text{
        font-size: 0.75rem;
     }
   }