.Body{
    background:  #1E1E25;
}
.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 4% 4.5%;
}
.sub{
    
    display: grid;
    grid-template-areas: "card card";
    padding: 5% 0;
    grid-row-gap: 2rem;
    grid-column-gap: 2rem;
}



.heading{
    font-family: 'Magilio';
    font-size: 4.5rem;
    color: #fff;
    padding-left: 2%;
}

@media screen and (min-width:320px) and (max-width: 760px) {
    .sub{
        grid-template-areas: "card";
        grid-row-gap: 1vh;
    }
    .heading{
        font-size:2.5rem ;
    }
    .container{
        margin-top: 5vh;
    }
}